import androidx.compose.runtime.CompositionLocalProvider
import com.varabyte.kobweb.core.AppGlobals
import com.varabyte.kobweb.navigation.BasePath
import com.varabyte.kobweb.navigation.Router
import com.varabyte.kobweb.navigation.UpdateHistoryMode
import com.varabyte.kobweb.navigation.remove
import com.varabyte.kobweb.silk.defer.DeferringHost
import kotlinx.browser.document
import kotlinx.browser.window
import org.jetbrains.compose.web.renderComposable

public fun main() {
    AppGlobals.initialize(mapOf("title" to "Ayush Sharma"))
    BasePath.set("")
    val router = Router()
    com.varabyte.kobweb.core.init.initKobweb(router) { ctx ->
        ctx.router.register("/") { com.example.portfoliowebsite.pages.HomePage() }

    }
    router.addRouteInterceptor {
        path = path.removeSuffix(".html").removeSuffix(".htm")
    }

    com.varabyte.kobweb.silk.init.additionalSilkInitialization = { ctx ->
        com.varabyte.kobweb.silk.init.initSilkWidgets(ctx)
        com.varabyte.kobweb.silk.init.initSilkWidgetsKobweb(ctx)
        ctx.theme.registerStyle("contact-card",
                com.example.portfoliowebsite.components.section.contact.component.ContactCardStyle)
        ctx.theme.registerStyle("footer",
                com.example.portfoliowebsite.components.section.footer.FooterStyle)
        ctx.theme.registerStyle("nav-header",
                com.example.portfoliowebsite.components.section.nav_header.NavHeaderStyle)
        ctx.theme.registerStyle("app-logo",
                com.example.portfoliowebsite.components.section.nav_header.component.AppLogoStyle)
        ctx.theme.registerStyle("nav-bar",
                com.example.portfoliowebsite.components.section.nav_header.component.NavBarStyle)
        ctx.theme.registerStyle("side-menu",
                com.example.portfoliowebsite.components.section.nav_header.component.SideMenuStyle)
        ctx.theme.registerStyle("skill-section-divider",
                com.example.portfoliowebsite.components.section.start.component.SkillSectionDividerStyle)
        ctx.theme.registerStyle("start-section-grid",
                com.example.portfoliowebsite.components.section.start.component.StartSectionGridStyle)
        ctx.theme.registerStyle("start-section-image",
                com.example.portfoliowebsite.components.section.start.component.StartSectionImageStyle)
        ctx.theme.registerStyle("work-item",
                com.example.portfoliowebsite.components.section.work.component.WorkItemStyle)
        ctx.theme.registerStyle("work-item-image",
                com.example.portfoliowebsite.components.section.work.component.WorkItemImageStyle)
        ctx.theme.registerStyle("work-item-overlay",
                com.example.portfoliowebsite.components.section.work.component.WorkItemOverlayStyle)
        ctx.theme.registerStyle("section-container",
                com.example.portfoliowebsite.components.widget.section.SectionContainerStyle)
        ctx.theme.registerStyle("skill-item",
                com.example.portfoliowebsite.components.widget.skill.SkillItemStyle)
        ctx.theme.registerStyle("text", com.example.portfoliowebsite.theme.font.TextStyle)
        ctx.theme.registerStyle("icon", com.example.portfoliowebsite.theme.icons.IconStyle)
        ctx.theme.registerVariant("-about",
                com.example.portfoliowebsite.components.section.about.AboutSectionVariant
        )
        ctx.theme.registerVariant("-about-section-skills-grid",
                com.example.portfoliowebsite.components.section.about.component.AboutSectionSkillsGridVariant
        )
        ctx.theme.registerVariant("-contact",
                com.example.portfoliowebsite.components.section.contact.ContactSectionVariant
        )
        ctx.theme.registerVariant("-nav-bar",
                com.example.portfoliowebsite.components.section.nav_header.component.NavBarLinkVariant
        )
        ctx.theme.registerVariant("-active-nav-bar",
                com.example.portfoliowebsite.components.section.nav_header.component.ActiveNavBarLinkVariant
        )
        ctx.theme.registerVariant("-side-menu",
                com.example.portfoliowebsite.components.section.nav_header.component.SideMenuLinkVariant
        )
        ctx.theme.registerVariant("-active-side-menu",
                com.example.portfoliowebsite.components.section.nav_header.component.ActiveSideMenuLinkVariant
        )
        ctx.theme.registerVariant("-start",
                com.example.portfoliowebsite.components.section.start.StartSectionVariant
        )
        ctx.theme.registerVariant("-work-section-container",
                com.example.portfoliowebsite.components.section.work.WorkSectionContainerVariant
        )
        ctx.theme.registerVariant("-work-items",
                com.example.portfoliowebsite.components.section.work.component.WorkItemsSimpleGridVariant
        )
        ctx.theme.registerVariant("-icon",
                com.example.portfoliowebsite.components.widget.button.IconButtonVariant
        )
        ctx.theme.registerVariant("-primary-icon",
                com.example.portfoliowebsite.components.widget.button.PrimaryIconButtonVariant
        )
        ctx.theme.registerVariant("-icon-button",
                com.example.portfoliowebsite.components.widget.button.LinkIconButtonVariant
        )
        ctx.theme.registerVariant("-primary",
                com.example.portfoliowebsite.components.widget.button.PrimaryButtonVariant
        )
        ctx.theme.registerVariant("-primary-button-with-icon-padding",
                com.example.portfoliowebsite.components.widget.button.PrimaryButtonWithIconPadding
        )
        ctx.theme.registerVariant("-skill-item",
                com.example.portfoliowebsite.components.widget.skill.SkillItemImageVariant
        )
        ctx.theme.registerVariant("-style-primary-color",
                com.example.portfoliowebsite.theme.font.TextStylePrimaryColor
        )
        ctx.theme.registerVariant("-style-secondary-color",
                com.example.portfoliowebsite.theme.font.TextStyleSecondaryColor
        )
        ctx.theme.registerVariant("-style-brand-color",
                com.example.portfoliowebsite.theme.font.TextStyleBrandColor
        )
        ctx.theme.registerVariant("-display-text-style",
                com.example.portfoliowebsite.theme.font.DisplayTextStyle
        )
        ctx.theme.registerVariant("-sub-display-text-style",
                com.example.portfoliowebsite.theme.font.SubDisplayTextStyle
        )
        ctx.theme.registerVariant("-title-text-style",
                com.example.portfoliowebsite.theme.font.TitleTextStyle
        )
        ctx.theme.registerVariant("-label-large-text-style",
                com.example.portfoliowebsite.theme.font.LabelLargeTextStyle
        )
        ctx.theme.registerVariant("-label-medium-text-style",
                com.example.portfoliowebsite.theme.font.LabelMediumTextStyle
        )
        ctx.theme.registerVariant("-body-large-text-style",
                com.example.portfoliowebsite.theme.font.BodyLargeTextStyle
        )
        ctx.theme.registerVariant("-body-medium-text-style",
                com.example.portfoliowebsite.theme.font.BodyMediumTextStyle
        )
        ctx.theme.registerVariant("-body-small-text-style",
                com.example.portfoliowebsite.theme.font.BodySmallTextStyle
        )
        ctx.theme.registerVariant("-brand-color",
                com.example.portfoliowebsite.theme.icons.IconBrandColorVariant
        )
        ctx.theme.registerVariant("-on-brand-color",
                com.example.portfoliowebsite.theme.icons.IconOnBrandColorVariant
        )
        ctx.theme.registerVariant("-large-icon-size",
                com.example.portfoliowebsite.theme.icons.LargeIconSize
        )
        ctx.theme.registerVariant("-medium-icon-size",
                com.example.portfoliowebsite.theme.icons.MediumIconSize
        )
        ctx.theme.registerKeyframes("side-menu-slide-in",
                com.example.portfoliowebsite.components.section.nav_header.component.SideMenuSlideInAnim)
        ctx.theme.registerKeyframes("start-section-grid-key-frames",
                com.example.portfoliowebsite.components.section.start.component.StartSectionGridKeyFrames)
        com.example.portfoliowebsite.initColorMode(ctx)
        com.example.portfoliowebsite.theme.initAppStyles(ctx)
        com.example.portfoliowebsite.theme.initSilkPalette(ctx)
    }

    router.tryRoutingTo(BasePath.remove(window.location.href.removePrefix(window.origin)),
            UpdateHistoryMode.REPLACE)

    // For SEO, we may bake the contents of a page in at build time. However, we will
    // overwrite them the first time we render this page with their composable, dynamic
    // versions. Think of this as poor man's hydration :)
    // See also: https://en.wikipedia.org/wiki/Hydration_(web_development)
    val root = document.getElementById("root")!!
    while (root.firstChild != null) { root.removeChild(root.firstChild!!) }

    renderComposable(rootElementId = "root") {
        com.example.portfoliowebsite.AppEntry {
            router.renderActivePage { DeferringHost { it() } }
        }
    }
}
